body {
  background-color: rgb(128, 144, 173);
  padding: 1%;
    height: 100%;
}

#root {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-line-pack: justify;
      align-content: space-between;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.precell {
  background-color: lightblue;
  border-color: darkgray;
  border-style: solid;
  border-width: 5px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border-radius: 10px;
  width: 96%;
  height: 92vh;
  text-align:center;
  vertical-align: middle;
  line-height: 600px;
  font-size: xx-large;
}

.cell {
  width: 15vw;
  height: 21vh;
  margin: 5px;
  -webkit-perspective: 1000px;
          perspective: 1000px;
}

.cell img {
  width: 15vw;
  height: 21vh;
  -o-object-fit: cover;
     object-fit: cover;
  border-color: darkgray;
  border-style: solid;
  border-width: 5px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border-radius: 10px;
}

.cell:hover img {
  border-color: lightblue;
}

.cell.deleted img {
  display: none;
}

.cell.selected img {
  border-color: rgb(0, 140, 255);
}

.cell.flip .flipper {
	-webkit-transform: rotateY(180deg);
	        transform: rotateY(180deg);
}

.flipper {
	-webkit-transition: 0.4s;
	-o-transition: 0.4s;
	transition: 0.4s;
	-webkit-transform-style: preserve-3d;
	        transform-style: preserve-3d;
	position: relative;
}

.front, .back {
	-webkit-backface-visibility: hidden;
	        backface-visibility: hidden;
	position: absolute;
	top: 0;
	left: 0;
}

.front {
	z-index: 2;
	/* for firefox 31 */
	-webkit-transform: rotateY(0deg);
	        transform: rotateY(0deg);
}

.back {
	-webkit-transform: rotateY(180deg);
	        transform: rotateY(180deg);
}


